.box {
  position: relative;
  overflow: hidden;
}

.box img {
  position: absolute;
  left: 0;
  transition: all 300ms ease-out;
}

.box .overbox {
  background-color: rgba(33, 33, 33, 0.58);
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  z-index: 100;
  transition: all 300ms ease-out;
  opacity: 0;
  width: 100%;
  padding-top: 5em;
}
.box,
.box .overbox {
  height: 230px;
}
.box:hover .overbox {
  opacity: 1;
}

.box .overtext {
  transition: all 300ms ease-out;
  transform: translateY(40px);
  -webkit-transform: translateY(40px);
}

.box .title {
  font-size: 1.3em;
  text-transform: uppercase;
  opacity: 0;
  transition-delay: 0.1s;
  transition-duration: 0.2s;
  text-align: center;
  padding: 10px;
  letter-spacing: 2px;
}
.box p.title {
  font-size: 1em;
  text-transform: capitalize;
  opacity: 0;
