  transition-delay: 0.1s;
  transition-duration: 0.2s;
  text-align: center;
  letter-spacing: 1px;
}

.box:hover .title,
.box:focus .title {
  opacity: 1;
  transform: translateY(0px);
  -webkit-transform: translateY(0px);
}

.box .tagline {
  font-size: 13px;
